import API from "../../api";

//AUTH CHECK
export var INFORM_CHECKING_AUTH = "INFORM_CHECKING_AUTH";
export var RECEIVE_AUTH_CHECK_RESULT = "RECEIVE_AUTH_CHECK_RESULT";
export var checkAuth = function checkAuth() {
  return function (dispatch, getState) {
    dispatch({
      type: INFORM_CHECKING_AUTH
    });
    return API.authCheck().then(function (authenticatedUser) {
      dispatch({
        type: RECEIVE_AUTH_CHECK_RESULT,
        authenticatedUser: authenticatedUser
      });
      return authenticatedUser;
    });
  };
};

//LOG OUT
export var INFORM_LOGGING_OUT = "INFORM_LOGGING_OUT";
export var RECEIVE_LOGGED_OUT_SUCCESS = "RECEIVE_LOGGED_OUT_SUCCESS";
export var logOut = function logOut() {
  return function (dispatch, getState) {
    dispatch({
      type: INFORM_LOGGING_OUT
    });
    API.logout().then(function () {
      dispatch({
        type: RECEIVE_LOGGED_OUT_SUCCESS
      });
    });
  };
};