import AuthSelector from "../../../app/selector/auth.selector";
import API from "../../../api";
export var INFORM_FETCH_INPOSTS = "INFORM_FETCH_INPOSTS";
export var RECEIVE_FETCH_INPOSTS = "RECEIVE_FETCH_INPOSTS";
